.filters {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.filters__dropdawn {
  list-style: none;
  text-transform: uppercase;
}

.filters__dropdawn:hover .list {
  max-height: 1rem;
}

@media (min-width: 700px) {
  .filters {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}


